.partners{
  &__content{
    margin-top: 1.875em;
  }
  &__nav{
    display: none;
  }
}

.partner-card{
  display: flex;
  align-items: center;
  padding: 1em .5em 1em 1.25em;
  background-color: #fff;
  box-shadow: $shadow;
  border-radius: .625em;
  min-height: 8.3125em;
  &:hover{
    background-color: $l-green;
    color: $black;
  }
  &__img{
    flex-shrink: 0;
    margin-right: 1em;
    img{
      max-width: 9.5em;
      max-height: 5em;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .partners{
    &__items{
      &:not(._show-all){
        .partner-card:nth-child(8)~.partner-card{
          display: none;
        }
      }
      &._show-all~.partners__nav{
        display: none;
      }
    }
    &__nav{
      display: flex;
      justify-content: center;
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .partners{
    .tabs{
      justify-content: space-between;
    }
    &__tab{
      margin-right: 0 !important;
    }
    &__items{
      gap: .625em;
    }
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }
}
