.text{
  li,
  p{
    font-size: 1em;
    line-height: 120%;
  }
  ul{
    padding-left: .25em;
    li{
      position: relative;
      padding-left: 1.125em;
      &:before{
        content: '•';
        position: absolute;
        left: 0;
        top: 0;
      }
    }
  }
  ol{
    counter-reset: section;
    padding-left: .25em;
    li{
      position: relative;
      counter-increment: section;
      padding-left: 1.125em;
      &:before {
        content: counter(section) '. ';
        position: absolute;
        left: 0;
        top: 0;
        width: auto;
        height: auto;
        background-color: transparent;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
  a{
    &:hover{
      color: $black;
      text-decoration: underline;
    }
  }
  small{
    font-size: .875em;
  }
}


