.header{
  &__inner{
    display: flex;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 15.25em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.header-top{
  padding: .625em 0;
  background: #f4f4f4;
  &__city{
    display: flex;
    align-items: center;
    img{
      margin-right: .75em;
      width: 2.25em;
    }
  }
}

.header-middle{
  margin-top: 1.25em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__phone{
    display: flex;
    align-items: center;
    svg{
      width: 1.625em;
      margin-right: .9375em;
    }
    .h2{
      font-weight: 400;
    }
  }
}

.header-search{
  width: 27.625em;
  height: 2.875em;
  position: relative;
  &__img{
    position: absolute;
    left: 1.875em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.625em;
  }
  input{
    width: 100%;
    height: 100%;
    font-size: .875em;
    padding: 0 5.28em;
    background-color: #fff;
    border: none;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.1);
    border-radius: 3.65em;
  }
}

.burger{
  display: none;
}

.header-bottom {
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    padding: 0 1.375em;
    height: 2.0625em;
    border-radius: 3.75em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      width: .625em;
      height: .625em;
      background-color: #00C808;
      top: -.125em;
      right: .1875em;
      border-radius: 50%;
    }
    span{
      font-size: 1em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  padding: 1em .625em;
  border-bottom: 1px solid $black;
  font-family: $font2;
  &__link{
    font-size: 1em;
    line-height: 125%;
    @include lastM(0 4.375em 0 0);
  }
}


@media screen and (max-width: 992px){
  .header{
    position: relative;
    z-index: 20;
    &__logo{
      width: 10em;
    }
    &._open{
      .header-bottom{
        opacity: 1;
        pointer-events: unset;
      }
      .header-search{
        opacity: 0;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .header-top{
    &__city{
      img{
        margin-right: .625em;
        width: 1.5em;
      }
    }
  }

  .header-middle{
    margin: 0;
    padding: .625em 0;
    &__inner{
      flex-wrap: wrap;
    }
    &__phone{
      display: none;
    }
  }

  .header-search{
    width: 100%;
    margin-top: .9375em;
  }

  .burger{
    position: relative;
    width: 2em;
    height: 2em;
    display: flex;
    padding: 0;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 1.875em;
    }
  }

  .header-bottom{
    margin-top: -3.25em;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background-color: #fff;
    padding: 1.875em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__inner{
      flex-direction: column;
    }
    &__btn{
      margin-top: 1.75em;
    }
  }

  .nav{
    border: none;
    flex-direction: column;
    &__link{
      @include lastM(0 0 1.5625em);
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 7.25em;
    }
  }

  .header-search{
    height: 2.5em;
    &__img{
      left: 1.25em;
      width: 1.25em;
    }
    input{
      font-size: .75em;
      padding-right: 1em;
      padding-left: 4.8em;
    }
  }
}
