.error404{
  padding: 6.25em 0;
  &__inner{
    display: grid;
    grid-template-columns: 20.875em calc(100% - 20.875em - 2.625em);
    justify-content: space-between;
    gap: 2.5em;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__desc{
    margin-top: 1.875em;
    max-width: 25em;
  }
  &__desc2{
    margin-top: 3.125em;
    max-width: 25em;
  }
  .text{
    margin-top: 1.25em;
    a{
      text-decoration: underline;
      &:hover{
        color: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .error404{
    padding: 5em 0 6.25em;
    &__inner{
      grid-template-columns: 15em calc(100% - 17.5em);
    }
    &__desc2{
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .error404{
    &__inner{
      grid-template-columns: 100%;
      position: relative;
      padding-top: 4.875em;
    }
    &__num{
      display: none;
    }
    &__title{
      position: absolute;
      right: 0;
      top: 0;
      width: 13.5em;
      text-align: right;
    }
    &__desc{
      margin-top: 0;
    }
    &__desc2{
      max-width: 16.25em;
    }
    .text{
      ol{
        @include grid100gap(.75em);
      }
    }
  }
}
