.licensy-slider{
  margin-top: 1.875em;
  position: relative;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 2.25em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 2.25em;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: 100%;
      margin-bottom: 2.5em;
    }
  }
}

.licensy-card{
  cursor: pointer;
  &:not(:hover){
    .licensy-card__img:after{
      opacity: 0;
    }
  }
  &__top{
    padding: 0 1.75em;
  }
  &__img{
    @include imgCover(133%);
    &:after{
      transition: all .3s;
    }
  }
  &__text{
    margin-top: 1.25em;
    text-align: center;
  }
}

.slider__pagination{
  position: relative;
  margin-top: 3.125em;
  height: .875em;
  .swiper-pagination{
    bottom: 0;
    transform: translateX(-50%) scale(1.75) !important;
    &-bullet{
      opacity: 1;
      background: $primary;
      //width: .875em !important;
      //height: .875em !important;
      &-active-prev,
      &-active-next{
        opacity: .5;
      }
      &-active-prev-prev,
      &-active-next-next{
        opacity: .2;
      }
    }
  }
}

.licensy-modal{
  .modal__dialog{
    width: 31.875em;
    @media screen and (min-width: 993px){
      padding: 1.875em;
    }
  }
  .licensy-card{
    pointer-events: none;
    &__text{
      .text14{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .licensy-slider{
    .swiper{
      &-slide{
        width: 50%;
      }
      &-nav{
        margin-bottom: 1.875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .licensy-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }
}
