.faq{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .select2{
    .list{
      width: 16.75em;
      padding: 1em 1.25em;
    }
    .option{
      font-size: 1em;
    }
  }
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.25em);
  }
}

.qa-card{
  background-color: #fff;
  box-shadow: $shadow;
  border-radius: .625em;
  padding: 1.25em;
  &._active{
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $green;
      }
    }
  }
  &__hidden{
    display: none;
    padding: 1.25em 0 0;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .faq{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    .select2{
      margin-top: .9375em;
      .list{
        left: 0;
        right: unset;
      }
    }
  }
}
