.online-services{
  .work-card__img:after{
    background: linear-gradient(180.00deg, rgba(255, 255, 255, 0.6) 42%,rgba(232, 237, 232, 0) 100%);
  }
  &__top{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__btn{
    margin-top: .625em;
    padding: 0 1.75em;
  }
}

.help{
  display: flex;
  align-items: center;
}

.help-form{
  width: 100%;
  text-align: center;
  &__fields{
    @include grid100gap(.625em);
    margin-top: 1.25em;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    text-transform: uppercase;
  }
}

.help-modal{
  .modal__dialog{
    width: 57em;
  }
  .text{
    ol,ul,p{
      @include lastM(0 0 1.25em);
    }
    ul ul{
      margin-bottom: 0 !important;
    }
  }
}


@media screen and (max-width: 992px){
  .online-services{
    .work-card__img{
      padding-bottom: 136.5%;
    }
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__info{
      gap: .9375em;
    }
    &__btn{
      margin-top: 1.25em;
    }
  }

  .help-modal{
    .modal__dialog{
      padding: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .help{
    margin-top: 3.75em;
  }
}
