.m-section{
  padding: 3.75em 0;
}

.services{
  &__desc{
    margin-top: .9375em;
  }
  &__nav{
    margin-top: 1.25em;
    display: flex;
    justify-content: flex-end;
  }
}

.slider{
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.services-slider{
  margin-top: 2em;
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        display: grid;
        grid-template-columns: repeat(4,calc(25% - 2.4375em / 4));
        gap: .8125em;
        transform: unset !important;
      }
      &-slide{
        padding-right: 0;
        width: 100%;
      }
    }
  }
}

.service-card{
  border-radius: .625em;
  box-shadow: $shadow;
  background-color: $l-green;
  display: block;
  position: relative;
  min-height: 15.625em;
  &:hover{
    color: #fff;
    background-color: $green;
    .service-card__icon{
      opacity: .2;
    }
  }
  &__icon{
    transition: inherit;
    display: block;
    width: 100%;
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    padding: 1.875em .25em 0 .9375em;
  }
  @media screen and (min-width: 993px){
    br{
      display: none;
    }
  }
}

.services2{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5625em));
    gap: 1.125em;
  }
}

.service-card2{
  position: relative;
  &__img{
    border-radius: 1.25em;
    @include imgCover(63.6%);
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.875em 3em 1.875em 1.875em;
  }
  &__link{
    position: absolute;
    top: 1.875em;
    left: 1.875em;
  }
  &__wrap{
    display: flex;
    align-items: center;
    margin-top: .75em;
    .service-card2__link{
      position: static;
      text-transform: uppercase;
      font-weight: 700;
      padding: 0 1.5em;
      margin-left: 1.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .m-section{
    padding: 2.5em 0;
  }

  .services{
    padding: 2.5em 0;
    overflow: hidden;
    &__nav{
      justify-content: unset;
    }
  }

  .services-slider{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        width: 12.75em;
        padding-right: 1.25em;
      }
    }
  }

  .service-card{
    min-height: 11.5em;
  }

  .service-card2{
    &__link{
      top: 1.25em;
      left: 1.25em;
    }
    &__content{
      padding: 1.25em;
    }
    &__wrap{
      flex-direction: column;
      align-items: flex-start;
      margin-top: .3125em;
      .service-card2__link{
        margin: .625em 0 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .services{
    &__link{
      padding: 0;
      width: 100%;
    }
  }

  .services2{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }
}
