.footer{
  margin-top: 3.75em;
  padding: 2.5em 0 1.25em;
  background-color: $l-blown;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 15.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    display: flex;
    align-items: flex-end;
    flex-direction: column;
  }
  &__phone{
    display: flex;
    align-items: center;
    svg{
      width: 1.625em;
      margin-right: .9375em;
    }
    &:hover{
      path{
        fill: $green;
      }
    }
  }
  .socials{
    margin-top: .625em;
  }
  &__middle{
    margin-top: 1.875em;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__bottom{
    margin-top: 1.25em;
  }
}

.footer-nav{
  @include lastM(0 3em 0 0);
  @include grid100gap(.9375em);
  max-width: 17.5em;
  &__links{
    margin-top: .3125em;
    @include grid100gap(.625em);
    a{
      font-size: .875em;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    margin-top: 2.5em;
    padding-bottom: 2.5em;
    &__top{
      align-items: flex-start;
    }
    &__logo{
      width: 11.5em;
    }
    &__phone{
      svg{
        width: 1.25em;
        margin-right: .625em;
      }
    }
    .socials{
      order: -1;
      margin: 0 0 .9375em;
    }
    &__middle{
      @include grid100gap(1.25em);
    }
    &__bottom{
      margin-top: 1.875em;
    }
  }

  .footer-nav{
    gap: 1.25em;
    &__links{
      display: none;
    }
  }
}


