.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 43.1875em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        padding: 3em;
        display: inline-block;
    }
    .close-btn{
        position: absolute;
        top: -.75em;
        right: -.9375em;
    }
    .req-form{
        margin: 2.5em auto 0;
        width: 32em;
        max-width: 100%;
    }
}

.close-btn{
    width: 1.875em;
    height: 1.875em;
    border-radius: 50%;
    @include center;
    flex-shrink: 0;
    box-shadow: $shadow;
    img{
        width: 1.125em;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog {
            padding: 3.125em 1.25em;
        }
        .close-btn{
            left: unset;
            right: 1.25em;
        }
        .req-form{
            margin-top: 1.875em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {

        }
    }
}
