.order{
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 17.375em - 1.875em) 17.375em;
    justify-content: space-between;
  }
  &__fields{
    @include grid100gap(1.25em);
    margin-top: 2.25em;
    .m-check{
      margin-top: -.625em;
    }
  }
  .fg{
    .nice-select{
      align-items: center;
      padding-top: 0;
      padding-bottom:  0;
    }
  }
  &__bottom{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    margin-top: .625em;
  }
  &__btn{
    margin-top: 1.25em;
    text-transform: uppercase;
  }
  &__sticky{
    position: sticky;
    top: 1em;
  }
}

.hint-btn{
  width: 1.125em;
  height: 1.125em;
  border-radius: 50%;
  background-color: $l-green;
  @include center;
  &:hover{
    background-color: darken($l-green,10%);
  }
  svg{
    width: .5em;
  }
}

.order-address{
  display: flex;
  align-items: center;
  margin-top: -.25em;
  position: relative;
  .fg{
    flex-grow: 1;
  }
  &__btn{
    text-transform: uppercase;
    margin-left: .8125em;
    flex-shrink: 0;
  }
  &__hint{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 9em;
  }
}

.order-hint{
  position: absolute;
  top: .25em;
  left: 11em;
  padding: 1.25em;
  background-color: #fff;
  z-index: 10;
  width: 38.125em;
  box-shadow: $shadow;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  .close-btn,
  &~.order-hint__bg{
    display: none;
  }
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
}

.order-quiz{
  margin-top: 6.625em;
  @include grid100gap(2.5em);
  &__info{
    margin-bottom: .9375em;
    @include grid100gap(.1875em);
  }
  &__items{
    @include grid100gap(.625em);
    padding-left: .9375em;
  }
  &__item{
    display: flex;
    align-items: center;
    .hint-btn{
      margin-left: 1.5em;
    }
  }
  &__count{
    display: flex;
    align-items: center;
    .cartcalc{
      margin-left: 2.5em;
    }
  }
}

.order-filial{
  margin-top: 2.5em;
  .centers{
    margin-top: 1.625em;
  }
}

.home-table{
  margin-top: 1.5em;
  table{
    width: 100%;
  }
  th,td{
    &:nth-child(1){
      width: 13.75em;
      padding-left: 3.125em;
    }
    &:nth-child(2){
      width: 23.75em;
    }
  }
  tr{
    position: relative;
  }
  th{
    text-align: left;
    padding-bottom: .3125em;
  }
  td{
    padding-top: 1em;
    padding-bottom: 1em;
    padding-right: 2em;
    &:last-child{
      padding-right: 0;
    }
  }
  .m-radio{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: .9375em;
    display: flex;
    align-items: center;
    input:checked~._bg{
      background-color: $l-green;
    }
    ._bg{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: .625em;
      z-index: -1;
      pointer-events: none;
      transition: all .3s;
    }
  }
}

.order-sum{
  padding: 1.25em .9375em;
  border-radius: 1.25em;
  background-color: $l-green;
  &__items{
    @include grid100gap(.625em);
    margin-top: 1.25em;
  }
  &__item{
    display: flex;
    justify-content: space-between;
    &--all{
      margin-top: .625em;
      padding-top: .625em;
      font-weight: 700;
      position: relative;
      &:after{
        content: '';
        position: absolute;
        height: 1px;
        width: 8.75em;
        max-width: 100%;
        background-color: $black;
        left: 0;
        top: 0;
      }
    }
  }
}

.order-sale{
  margin-top: 1.5em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__hint{
    font-family: $font2;
    padding: 0 .75em;
    height: 1.875em;
    display: flex;
    align-items: center;
    border-radius: 2.3125em;
    border: 1px solid $black;
  }
}

.order-benefit{
  margin-top: .625em;
  display: flex;
  justify-content: space-between;
  .m-check{
    margin-left: .5em;
    flex-shrink: 0;
    label{
      padding-left: 1.25em;
    }
  }
  &__desc{
    margin-top: .625em;
  }
}

.order-info{
  margin-top: 8em;
}


@media screen and (max-width: 992px){
  .order{
    &__inner{
      grid-template-columns: 100%;
      gap: 5em;
      position: relative;
      padding-bottom: 4.75em;
    }
    &__fields{
      margin-top: 1.875em;
    }
    &__btn{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      min-width: 16em;
    }
    &__sticky{
      position: static;
    }
  }

  .order-hint{
    position: fixed;
    top: 2em;
    left: 0;
    width: 100%;
    padding: 1.25em .625em;
    &._active{
      &~.order-hint__bg{
        //display: block;
      }
    }
    .close-btn{
      position: absolute;
      top: -.9375em;
      right: 1.25em;
      display: flex;
    }
  }

  .order-quiz{
    margin-top: 2.5em;
    &__item
    .hint-btn{
      margin-left: .9375em;
    }
  }

  .order-info{
    margin-top: 5em;
  }
}


@media screen and (max-width: 576px){
  .order{

  }

  .order-address{
    display: block;
    &__btn{
      margin: 1.875em 0 0 auto;
    }
  }

  .order-quiz{
    &__count{
      padding-right: 2.5em;
    }
  }

  .home-table{
    margin-top: 1.25em;
    table{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      display: block;
    }
    tr{
      padding: .625em;
      @include grid100gap(1.25em);
    }
    td{
      padding-top: 0;
      padding-bottom: 0;
      width: 100% !important;
      padding-left: 3em;
    }
  }
}
