.news-page{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.875em;
  }
}

.select2{
  .nice-select{
    background-color: transparent;
    padding: 0;
    min-height: unset;
    border: none;
    align-items: center;
    &:before{
      background-image: url(../img/arrow-down2.svg);
      width: .8125em;
      height: .5em;
      margin-left: .625em;
    }
    .list{
      left: unset;
      right: 0;
    }
    .current{
      font-size: 1.125em;
      font-weight: 700;
      font-family: $font2;
    }
    .option.selected{
      display: none;
    }
  }
}

.news-group{
  padding: 2.5em 0;
  background-color: $l-blown;
  &--white{
    background-color: transparent;
  }
}

.news-card2{
  display: flex;
  flex-direction: column;
  &:hover{
    .news-card2__img:after{
      opacity: 1;
    }
  }
  &__img{
    @include imgCover(58%);
    border-radius: .625em;
    &:after{
      transition: all .3s;
      background: rgba(22, 94, 25, 0.2);
      opacity: 0;
    }
  }
  &__title{
    margin-top: .9375em;
    @include truncate(3);
  }
  &__date{
    padding-top: .3125em;
    margin-top: auto;
    text-align: right;
  }
}

.news-item{
  &__top{
    display: grid;
    grid-template-columns: calc(100% - 7em) 5.25em;
    justify-content: space-between;
    gap: 1em;
  }
  &__text{
    margin-top: 1.875em;
  }
}

.news-info{
  position: relative;
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 2.5em;
    color: #fff;
  }
  &__img{
    border-radius: 1.25em;
    @include imgCover(43.4%);
    &:after{
      background: rgba(0, 0, 0, 0.3);
    }
  }
}

.news-info2{
  display: flex;
  flex-direction: column;
  align-items: center;
  &__views{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: .9375em;
    img{
      width: 1.25em;
      margin-right: .625em;
    }
  }
}


@media screen and (max-width: 992px){
  .news-page{
    &__top{
      margin-bottom: 1.625em;
    }
  }

  .news-item{
    &__top{
      grid-template-columns: 100%;
    }
    &__text{
      margin-top: 1.25em;
    }
  }

  .news-info{
    &__content{
      padding: 1.25em;
    }
  }

  .news-info2{
    justify-content: space-between;
    flex-direction: row;
    &__views{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .news-card2{
    &__img{
      padding-bottom: 44.65%;
    }
  }

  .news-info{
    &__content{
      padding: .75em;
    }
    &__img{
      padding-bottom: 125.6%;
    }
  }
}
