.info{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
}

.info-card{
  border-radius: 1.25em;
  box-shadow: $shadow;
  position: relative;
  display: block;
  &:hover{
    .info-card__content{
      color: #fff;
      background-color: $green;
    }
    .info-card__link{
      border-color: #fff;
      &:not(:hover){
        color: #fff;
      }
    }
  }
  &__img{
    @include imgCover(39.3%);
    border-radius: inherit;
  }
  &__content{
    transition: all .3s;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    @include center;
    flex-direction: column;
    text-align: center;
    padding: 1.125em 1.5em;
  }
  &__desc{
    margin-top: .9375em;
  }
  &__link{
    position: absolute;
    top: 1.25em;
    left: 1.25em;
    z-index: 3;
  }
  &--big{
    grid-column: 1/-1;
    @media screen and (min-width: 577px){
      .info-card__img{
        padding-bottom: 31.3%;
      }
    }
    .info-card__link{
      position: static;
      margin-top: 1.25em;
    }
  }
}

.news{
  padding: 3.125em 0;
  margin: 3.75em 0;
  background-color: $l-blown;
  &__grid{
    margin-top: .9375em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.news-card{
  position: relative;
  color: #fff;
  &:hover{
    color: #fff;
  }
  &__img{
    border-radius: 1.25em;
    @include imgCover(69%);
    min-height: 100%;
    &:after{
      background: rgba(0, 0, 0, 0.3);
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    padding: .625em;
    z-index: 2;
    @include grid100gap(.3125em);
  }
  &__title{
    font-size: 1.125em;
    line-height: 130%;
    font-weight: 700;
    @include truncate(2);
  }
  &__desc{
    @include truncate(2);
    margin: .9375em 0 .25em;
    display: none;
  }
  &__date{
    text-align: right;
  }
  @media screen and (min-width: 993px){
    &._big2,
    &._big{
      grid-column: 1/3;
      grid-row: 1/3;
      .news-card__content{
        padding: 1.25em 2.1875em;
      }
      .news-card__title{
        font-size: 1.25em;
        -webkit-line-clamp: 4;
      }
      .news-card__desc{
        display: -webkit-box;
      }
    }
    &._big2{
      grid-column: 1/-1;
      .news-card__img{
        padding-bottom: 38.9%;
      }
    }
  }
}

.banners-slider{
  border-radius: 1.25em;
  box-shadow: $shadow;
  overflow: hidden;
  position: relative;
  .swiper{
    &-nav{
      position: absolute;
      top: 2.5em;
      right: 2.5em;
    }
  }
}

.banner{
  position: relative;
  &__img{
    @include imgCover(44.2%);
    border-radius: 1.25em;
  }
  .m-backdrop:after{
    background: linear-gradient(180.00deg, rgba(255, 255, 255, 0),rgba(255, 255, 255, 0.5) 100%);
  }
  &__content{
    position: absolute;
    right: 0;
    bottom: 0;
    width: 37.5em;
    max-width: 100%;
    padding: 3.125em 2.5em;
    @include grid100gap(3em);
  }
  &__content2{
    border-radius: 1.25em;
    text-align: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 3.75em;
    width: 32.625em;
    max-width: 100%;
    padding: 2.5em 3.125em;
    box-shadow: $shadow;
    background: linear-gradient(180deg, #d6d4d1 0%, #eaeae9 16.57%, #dddbd7 31.96%, #fff 56.63%, #dddbd7 100%);
    @include grid100gap(1.25em);
    .h2{
      padding: 0 1em;
    }
  }
  &__rect{
    @include center;
    border-radius: .3125em;
    border: 1px solid $black;
    height: 5.5em;
  }
}

.swiper-nav{
  display: flex;
  align-items: center;
  .swiper-button{
    margin-left: 1.875em;
    position: static;
  }
}

.swiper-button{
  width: 1.75em;
  height: 1.75em;
  @include center;
  margin: 0;
  svg{
    width: 100%;
  }
  &:hover{
    path{
      fill: $green;
    }
  }
}


@media screen and (max-width: 992px){
  .info-card{
    &__img{
      padding-bottom: 62.5%;
      &:after{
        //background: linear-gradient(179deg, #fff 0%, #fff 0.01%, #fff 100%);
      }
    }
  }

  .news{
    margin: 2.5em 0;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .banners-slider{
    .swiper{
      &-nav{
        top: 1.25em;
        right: 1.25em;
      }
    }
  }

  .banner{
    &__content2,
    &__content{
      gap: 1.25em;
      padding: 1.875em .9375em;
    }
    &__rect{
      height: 3em;
    }
    &__content2{
      bottom: 0;
      background: transparent;
      box-shadow: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .info{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .banner{
    &__img{
      padding-bottom: 125%;
    }
  }
}
