@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.5625em;
    height: 2.875em;
    border-radius: .9375em;
    font-weight: 700;
    font-family: $font2;
    span{
        font-size: 1.125em;
        line-height: 120%;
    }
    &:focus {
        box-shadow: none;
    }
}

.m-btn-small{
    height: 1.875em;
    padding: 0 .75em;
    font-weight: 400;
    border-radius: 2.3125em;
    span{
        font-size: 1em;
    }
}

.m-btn-green{
    background-color: $primary;
    color: #fff;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.m-btn-green-outline{
    border: 1px solid #039475;
    color: $black;
    &:hover{
        color: $black;
        background-color: $l-green;
    }
}

.m-btn-black-outline{
    border: 1px solid $black;
    color: $black;
    &:hover{
        color: $black;
        background-color: $l-green;
    }
}
