.pagination{
  @include center;
  margin-top: 2.5em;
  li:not(.prev):not(.next){
    margin: 0 .125em;
    a{
      height: 1.72em;
      min-width: 1.72em;
      padding: 0 .25em;
      border-radius: 50%;
      @include center;
      font-size: .875em;
      line-height: 143%;
      //color: $light-text;
    }
    svg{
      width: 1.0625em;
      height: auto;
    }
    &.active{
      pointer-events: none;
      a{
        box-shadow: 0 .1875em .3125em rgba(0, 0, 0, 0.02);
        background-color: $l-green;
      }
    }
    &:hover,
    &.active{
      a{
        //color: $white;
      }
    }
  }
  .active{

  }
  a{
    @include center;
    width: 100%;
    height: 100%;
  }
  .next2,
  .prev2,
  .next,
  .prev{
    a{
      width: 1.5em;
      height: 1.5em;
      display: block;
      background-color: #fff;
      box-shadow: 0 .25em .375em rgba(0, 0, 0, 0.02);
      @include bgImgSize(.4375em);
      border-radius: 50%;
      &:hover{
        background-color: $l-green;
      }
    }
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $green;
      }
    }
  }
  .prev2{
    margin-right: .625em;
    a{
      background-size: .5625em auto;
      background-image: url(../img/prev2.svg);
    }
  }
  .prev{
    margin-right: .5em;
    a{
      background-image: url(../img/prev.svg);
    }
  }
  .next{
    margin-left: .5em;
    a{
      background-image: url(../img/next.svg);
    }
  }
  .next2{
    margin-left: .625em;
    a{
      background-image: url(../img/next2.svg);
    }
  }
}


@media screen and (max-width: 992px){
  .pagination{
    margin-top: 1.875em;
  }
}


