.req{
  padding: 3.125em 0;
  margin: 3.75em 0 -3.75em;
  box-shadow: $shadow;
  &__top{
    @include grid100gap(1.25em);
    text-align: center;
  }
  .req-form{
    width: 32em;
    max-width: 100%;
    margin: 1.875em auto 0;
  }
}

.req-form{
  @include grid100gap(.625em);
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.875em;
  }
}

.contacts{
  &__tabs,
  &__content{
    margin-top: 1.875em;
  }
}

.departments{
  grid-auto-flow: dense;
}

.contact-card{
  grid-column: span 2;
  padding: 1.875em;
  border-radius: .625em;
  background-color: $l-green;
  transition: all .3s;
  &__desc{
    margin-top: .9375em;
  }
  &__items{
    margin-top: 1.875em;
    @include grid100gap(.625em);
  }
  &__item{
    display: flex;
    align-items: center;
    svg,
    img{
      width: 1.25em;
      margin-right: .625em;
    }
  }
  &__group{
    margin-top: 1.25em;
    .contact-card__items{
      margin: 0;
      gap: .4375em;
    }
  }
  &--big{
    grid-column: span 3;
    @media screen and (min-width: 993px){

    }
  }
}

.centers{
  &__top{
    max-height: 16.5em;
    overflow-y: auto;
    @include scroll;
  }
  &__bottom{
    margin-top: 2.5em;
    position: relative;
    .map{
      width: calc(100% + 27em);
    }
  }
  &__wrap{
    overflow: hidden;
  }
}

.map{
  height: 40em;
  .ymaps-2-1-79-controls__control{
    left: unset !important;
    right: 28.5em !important;
    top: 16.5em !important;
    width: 3em !important;
    height: 7em !important;
    box-shadow: 0px 1.7px 5px 0px rgba(0, 0, 0, 0.04), 0px 0.9px 2.7px 0px rgba(0, 0, 0, 0.03), 0px 0.5px 1.5px 0px rgba(0, 0, 0, 0.03), 0px 0.3px 0.8px 0px rgba(0, 0, 0, 0.02), 0px 0.1px 0.3px 0px rgba(0, 0, 0, 0.01), 0px 4px 12px 0px rgba(0, 0, 0, 0.05);
    background-color: #fff;
    border-radius: 6.25em;
  }
  .ymaps-2-1-79-zoom{
    width: 100% !important;
    height: 100% !important;
    padding: 0;
  }
  .ymaps-2-1-79-zoom__scale{
    display: none;
  }
  .ymaps-2-1-79-zoom{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .ymaps-2-1-79-zoom__plus{
    top: 50%;
    transform: translateY(-100%);
    background-image: url(../img/plus.svg);
    border-radius: 6.25em 6.25em 0 0;
  }
  .ymaps-2-1-79-zoom__minus{
    bottom: 50% ;
    transform: translateY(100%);
    background-image: url(../img/minus.svg);
    border-radius: 0 0 6.25em 6.25em;
  }
  .ymaps-2-1-79-zoom__button{
    width: 100%;
    height: 50%;
    background-color: transparent;
    box-shadow: unset;
    border: none;
    border-radius: unset;
    padding: 0;
    @include bgImgSize(1.5em);
  }
  .ymaps-2-1-79-zoom__icon{
    display: none;
  }
}

.center-item{
  display: flex;
  align-items: flex-start;
  padding: .9375em;
  border-radius: .625em;
  cursor: pointer;
  transition: all .3s;
  &._active{
    background-color: $l-green;
  }
  &__col{
    margin-right: .75em;
  }
  &__col2{
    @include grid100gap(.625em);
  }
  &__num{
    @include sameNumWidth;
  }
}

.centers-slider{
  z-index: 4;
  position: absolute;
  border-radius: 0 .625em .625em .625em;
  width: 27em;
  padding: 1.5em;
  background-color: #fff;
  box-shadow: 0 .25em 1.25em 0 rgba(0, 0, 0, 0.1);
  .swiper-container{
    overflow: hidden;
  }

  .swiper-nav{
    position: absolute;
    right: 1.5em;
    bottom: 1.5em;
  }
}

.center-card{
  &__wrap{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: calc(100% - 10em) 9.75em;
    justify-content: space-between;
    gap: .9375em;
  }
  .contact-card__items{
    margin: 0;
  }
  &__chef{
    @include grid100gap(.5em);
  }
  &__bottom{
    margin-top: 1.25em;
  }
}

.work-time{
  @include grid100gap(.875em);
  margin-top: .75em;
  &__item{
    display: grid;
    grid-template-columns: 2.1875em calc(100% - 2.1875em);
    &._disable{
      .work-time__day{
        color: #969696;
      }
    }
  }
}



@media screen and (max-width: 992px){
  .req{
    margin: 2.5em 0 -2.5em;
    .req-form{
      margin-top: 1.25em;
    }
  }

  .req-form{
    &__nav{
      margin-top: 1.25em;
    }
  }

  .contact-card{
    grid-column: unset;
    padding: 1.875em 1.25em;
  }

  .centers{
    &__top{
      max-height: 18.5em;
    }
    &__bottom{
      .map{
        width: 100%;
      }
    }
  }

  .center-item{
    .h1{
      font-size: 1.5em;
    }
    .text18{
      font-size: 1em;
    }
  }

  .map{
    height: 25em;
    .ymaps-2-1-79-controls__control{
      right: .25em !important;
      top: 9em !important;
    }
  }

  .centers-slider{
    position: relative;
    width: calc(100% + 2.5em);
    margin-left: -1.25em;
    .swiper-nav{
      right: 1.25em;
      bottom: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__tabs{
      flex-direction: column;
      align-items: flex-start;
    }
    &__tab{
      @include lastM(0 0 1.25em);
    }
    &__content{
      margin-top: 1.5em;
    }
  }

  .centers{
    &__top{
      max-height: 28em;
    }
  }

  .center-item{
    padding-right: .5em;
    padding-left: .75em;
    &__col{
      margin-right: .5em;
    }
  }

  .center-card{
    &__wrap{
      grid-template-columns: 100%;
    }
  }
}
