.page-top{
    padding: 1.25em 0 0;
}

.breadcrumbs {
    margin-bottom: 1.5em;
    &:last-child{
        margin-bottom: 0;
    }
    li{
        position: relative;
        display: inline;
        color: $placeholder;
        &:not(:first-child) {
            margin-left: .75em;
            &:before {
                content: '>';
                position: absolute;
                z-index: 1;
                top: .25em;
                right: 100%;
                margin-right: .325em;
                font-size: .75em;
                line-height: 120%;
            }
        }
    }
    a{
        font-size: .75em;
        line-height: 120%;
    }
}
