.tabs{
  display: flex;
}

.tab{
  display: flex;
  align-items: center;
  position: relative;
  color: $placeholder;
  transition: all .3s;
  @include lastM(0 5em 0 0);
  &:hover{
    color: $black;
    cursor: pointer;
  }
  &._active{
    color: $black;
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
