.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1em;
    line-height: 138%;
    margin-bottom: .625em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0;
    border: 1px solid #d9d9d9;
    padding: 0 1.25em 0 2.14em;
    height: 2.86em;
    width: 100%;
    font-size: .875em;
    background-color: transparent;
    color: $black;
    &::placeholder {
      color: $placeholder;
    }
    &:focus {
      border-color: $black;
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    height: unset;
    min-height: 2.5em;
    padding: 1em 1.875em;
    white-space: unset;
    font-size: 1em;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &:focus{

    }
    &:before{
      content: '';
      order: 3;
      border-left: $black;
      flex-shrink: 0;
      width: 1.5em;
      height: 1.5em;
      background-image: url(../img/arrow-down.svg);
      @include bgImgSize(100%);
      margin-left: 1em;
    }
    .current{
      font-size: .875em;
      line-height: 136%;
      max-width: 24em;
    }
    .list{
      border-radius: .3125em;
      box-shadow: $shadow;
      left: -1px;
      border: none;
      background: #FFF;
      padding: 1em 1.5em;
      max-height: 20em;
      min-width: 100%;
      width: auto;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
      @include grid100gap(.625em);
    }
    .option{
      height: unset;
      font-size: 1.125em;
      line-height: 120%;
      padding: 0;
      display: flex;
      align-items: center;
      border: none;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: transparent;
        font-weight: 400;
        color: $primary;
      }
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2em;
    min-height: 1.25em;
    span{
      font-size: .875em;
      line-height: 120%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid #d9d9d9;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        //background-color: $primary;
        //border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .125em;
      background-image: url(../img/check.svg);
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    padding-left: 1.875em;
    &:before{
      border-radius: 50%;
      border-color: $placeholder;
      background-image: url(../img/radio.svg);
    }
  }
}

.cartcalc{
  flex-shrink: 0;
  max-width: 100%;
  width: 5.625rem;
  height: 3.5em;
  display: flex;
  justify-content: space-between;
  button,
  input{
    height: 100%;
    min-height: 100%;
  }
  input{
    width: 1.6em;
    border-radius: .2em;
    border: 1px solid $placeholder;
    color: $black;
    font-size: 1.25em;
    text-align: center;
  }
  svg{
    width: 100%;
    height: auto;
  }
  button{
    width: .8125em;
    @include center;
    &:hover{
      path{
        fill: $green;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    .nice-select{
      padding: .625em .625em .625em 1.25em;
      .list{
        padding: .625em 1.25em;
      }
      .option{
        font-size: .875em;
      }
    }
  }
}


