.text-page{
  &__tags{
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 1.25em 2.5em;
    width: 40em;
    max-width: 100%;
    margin-top: 3.125em;
  }
}

.guide{

}

.awards{
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.875em);
  }
}

.award-card{
  display: grid;
  grid-template-columns: 11.0625em calc(100% - 16.5625em);
  justify-content: space-between;
  align-items: center;
  gap: 1.25em;
  padding: 1.25em 4.25em;
  background-color: #fff;
  border-radius: 1.25em;
  box-shadow: $shadow;
  &__img{
    img{
      width: 100%;
    }
  }
}

.workers{
  .h1{
    max-width: 19em;
  }
}

.workers-table{
  margin-top: 1.875em;
  border-radius: 1.25em;
  background-color: #fff;
  box-shadow: $shadow;
  table{
    border-collapse: collapse;
    width: 100%;
    text-align: center;
  }
  tr{
    position: relative;
  }
  th,td{
    width: 50%;
  }
  th{
    height: 5.5em;
    &:first-child{
      &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #fff;
        left: 0;
        top: 0;
        border-radius: 1.25em;
        box-shadow: $shadow;
      }
    }
  }
  .h2{
    position: relative;
    z-index: 2;
  }
  td{
    padding: 1em 0;
    height: 4.375em;
    border-right: 1px solid rgba(135, 135, 135, 0.5);
    border-bottom: 1px solid rgba(135, 135, 135, 0.5);
    &:last-child{
      border-right: none;
    }
  }
  tr:last-child{
    td{
      border-bottom: none;
    }
  }
}


@media screen and (max-width: 992px){
  .guide{
    .text{

    }
  }

  .award-card{
    padding: 1.25em;
    grid-template-columns: 6.25em calc(100% - 7.5em);
  }

  .workers-table{
    margin-top: 1.25em;
    th{
      height: 3.75em;
    }
    .h2{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .text-page{
    &__tags{
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .award-card{
    grid-template-columns: 100%;
    &__img{
      width: 6.25em;
      margin: 0 auto;
    }
  }
}
