.work-way{
  &__grid{
    margin-top: 1.875em;
  }
}

.work-card{
  position: relative;
  border-radius: .625em;
  &:hover{
    @media screen and (min-width: 993px){
      .work-card__content{
        background: $green;
        color: #fff;
      }
      .text{
        &._view1{
          display: none;
        }
        &._view2{
          display: block;
        }
      }
    }
  }
  &__img{
    border-radius: inherit;
    @include imgCover(150%);
    &:after{
      background: linear-gradient(180.00deg, rgba(255, 255, 255, 0),rgba(#e8ede8 , 0.5) 100%);
    }
  }
  &__content{
    border-radius: inherit;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: 2.5em 1.5em 1em 1.875em;
    transition: all .3s;
  }
  .text{
    margin-top: 1.25em;
    &._view2{
      display: none;
    }
  }
}

.consult{
  &__grid{
    display: grid;
    grid-template-columns: 30em calc(100% - 33.5em);
    gap: 1.875em 3.5em;
  }
  &__col{
    max-width: 29em;
  }
}

.consult-form{
  @include grid100gap(.625em);
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 1.25em;
  }
  &__btn{
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){
  .work-card{
    &__img{
      padding-bottom: 100%;
    }
    &__content{
      padding: 2.5em 1.25em;
    }
  }

  .consult{
    &__grid{
      grid-template-columns: 18em calc(100% - 19.875em);
      gap: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .consult{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
