.structure{
  &__items{
    margin-top: .625em;
    width: calc(100% + 1.25em);
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  &__items2{
    margin-top: 7.5em;
    align-items: flex-start;
  }
}

.structure-card,
.structure-card2{
  background-color: $l-green;
  padding: 1.25em;
  border-radius: .625em;
  box-shadow: $shadow;
}

.structure-card{
  width: calc(100% / 3 - 1.25em);
  margin: 1.25em 1.25em 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  &__top{
    text-align: center;
    @include grid100gap(.4375em);
  }
  &__items{
    margin-top: .9375em;
    @include grid100gap(.625em);
  }
  .contact-card__item{
    justify-content: center;
  }
  &__link{
    margin-top: 1em;
    &:hover{
      background-color: $primary;
      border-color: $primary;
      color: #fff;
    }
  }
}

.structure-card2{
  min-height: 12.1875em;
  transition: all .3s;
  &._active{
    .structure-card2__toggle{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &:hover{
    background-color: $green;
    color: #fff;
    path{
      fill: #fff;
    }
  }
  &__top{
    cursor: pointer;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  &__toggle{
    width: 1.75em;
    @include center;
    flex-shrink: 0;
    svg{
      width: 100%;
    }
  }
  &__items{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: .875em;
  }
  .contact-card__item{
    transition: unset;
    @include lastM(0 0 .9375em);
    &:hover{
      color: #fff;
      opacity: .8;
    }
  }
  &__hidden{
    margin-top: 1.25em;
    display: none;
  }
  &__person{
    @include grid100gap(.4375em);
  }
  &__desc{
    margin-top: .4375em;
  }
}


@media screen and (max-width: 992px){
  .structure{
    &__items{
      margin-top: 1.25em;
      width: calc(100% + .625em);
    }
    &__items2{
      margin-top: 3.375em;
    }
  }

  .structure-card{
    width: calc(50% - .625em);
    margin: .625em .625em 0 0;
  }
}


@media screen and (max-width: 576px){
  .structure{
    &__items2{
      gap: .625em;
    }
  }

  .structure-card{
    width: calc(100% - .625em);
  }

  .structure-card2{
    min-height: unset;
  }
}
